.home_bg {
  background-color: #161d29;
  min-height: max-content;
}
.home_bg > .MuiGrid-container {
  padding: 40px;
  justify-content: center !important;
}
.island_sx {
  display: flex;
  height: 200px;
}
.one {
  background-image: url("../assets/images/Island/SYSTEMATIC_ASSEMBLY.png");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}
.two {
  background-image: url("../assets/images/Island/PRODUCTIVE_ECOLOGY.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.three {
  background-image: url("../assets/images/Island/RECONTEXTUALISING_NARRATIVES.png");
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
}
.four {
  background-image: url("../assets/images/Island/EMPOWERING_COMMUNITIES.png");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}
.five {
  background-image: url("../assets/images/Island/EXPLORING_POSSIBILITIES.png");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}
.six {
  background-image: url("../assets/images/Island/RE-CALIBRATING_WELLNESS.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.header_title {
  margin: 16px 0px;
}
.home_btn {
  transition: inherit;
  color: #d5d5d5 !important;
  font-size: 10px !important;
  margin: 0px 8px !important;
  background: rgba(43, 43, 43, 0.75) !important;
  box-shadow: none !important;
  border: 5px solid #2b2b2b !important;
  border-radius: 0px !important;
  transition: all 0.2s ease, visibility 0s !important;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
}
.home_content {
  margin: 20px;
  padding: 30px;
  border-radius: 0px !important;
  border: none !important;
  background: transparent !important;
  box-shadow: none !important;
  color: #d5d5d5 !important;
  /* box-shadow: 6px 12px 0px 4px rgb(0 0 255 / 30%) !important;
  color: #d5d5d5 !important;
  background-color: #423737 !important;
  border: 5px solid #957b5f !important; */
}
.home_content > .MuiGrid-container > .MuiGrid-item {
  text-align: center;
  /* min-height: 360px; */
}
.home_btn:hover {
  background-color: #423737 !important;
  transform: translateY(-10px);
  -webkit-transform: translateY(-10px);
  -moz-transform: translateY(-10px);
  -ms-transform: translateY(-10px);
  -o-transform: translateY(-10px);
}
.iframe_wrapper {
  min-height: 500px;
}

.html5-video-player:not(.ytp-transparent),
.html5-video-player.unstarted-mode,
.html5-video-player.ad-showing,
.html5-video-player.ended-mode,
.html5-video-player.ytp-fullscreen {
  background-color: #161d29 !important;
}
.nxt_stop {
  display: flex;
  flex-direction: column !important;
  align-items: center;
  justify-content: center;
}
.message {
  padding: 0px 40px;
}
@media only screen and (max-width: 600px) {
  .home_bg {
    padding-top: 45px;
  }
  .message {
    padding: 0px 40px 10px;
  }
  .home_content {
    margin: 20px 5px;
    padding: 15px;
  }
  .home_bg > .MuiGrid-container {
    padding: 0px;
  }
  .home_content > .MuiGrid-container > .MuiGrid-item {
    min-height: 100%;
  }
  .scale {
    transform: scale(0.65);
    -moz-transform: scale(0.65);
    -ms-transform: scale(0.65);
    -o-transform: scale(0.65);
    -webkit-transform: scale(0.65);
  }
  /* .three {
    background-position: center;
  } */
}
