.compass_backdrop {
  position: fixed;
  left: 30px;
  width: 100%;
  height: 100%;
}
.compass {
  position: fixed;
  top: 65px;
  left: 30px;
}
.landing_menu {
  position: fixed;
  bottom: 0;
  left: 30px;
}
.compass > img {
  width: 60px;
  cursor: pointer;
}
.compass > ul,
.landing_menu > ul {
  width: max-content;
  list-style-type: none;
  padding: 0;
}
.compass > ul > li,
.landing_menu > ul > li {
  margin: 10px 0px 0px 0px;
  cursor: pointer;
}
.compass > ul > li:hover,
.landing_menu > ul > li:hover,
.landing_menu > ul > li:active,
.compass > ul > li:active {
  color: #efb385;
}
#about {
  scroll-margin-top: 40px;
}
#message {
  scroll-margin-top: 0px;
}
.drawer > .MuiPaper-root {
  background-color: #2b2b2b !important;
  color: #ffffff;
  width: 100%;
}
.drawer > .MuiPaper-root > ul {
  padding: 0;
  list-style-type: none;
  line-height: 45px;
}
.drawer > .MuiPaper-root > ul > li {
  text-align: center;
}
.close_btn {
  text-align: end;
  padding-right: 25px;
}
